@import url("https://fonts.googleapis.com/css?family=Roboto:300,300i,400&subset=cyrillic");
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

a {
  text-decoration: none;
  color: #000;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 1em;
}

li {
  list-style: none;
}

.icon-close {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-heart {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-menu-button {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-share {
  width: 1em;
  height: 1em;
  fill: initial;
}

.body {
  background-color: #FAFAFA;
}

.out {
  max-width: 1218px;
  margin: 0 auto;
  padding-left: 0;
}

.main {
  position: relative;
}

.header {
  margin-top: 43px;
  position: relative;
}

@media only screen and (max-width: 650px) {
  .header {
    margin-top: 20px;
  }
}

.header__logo {
  float: left;
  margin-left: 14px;
}

.header__nav {
  padding-top: 17px;
  margin-bottom: 24px;
}

@media only screen and (max-width: 570px) {
  .header__nav {
    margin-bottom: 44px;
  }
}

.header__user {
  position: relative;
  height: 62px;
  height: 62px;
  height: calc(110px + (62 - 110) * ((100vw - 400px) / (700 - 400)));
}

@media (max-width: 400px) {
  .header__user {
    height: 110px;
  }
}

@media (min-width: 700px) {
  .header__user {
    height: 62px;
  }
}

.h2 {
  font-family: 'Roboto', sans-serif;
  font-size: 22px;
  line-height: calc(20/22);
}

.text {
  font-family: 'Roboto', sans-serif;
  font-size: 15px;
  line-height: calc(18/15);
  letter-spacing: 0.15px;
}

.text_author {
  color: #007FCE;
  letter-spacing: 0.7px;
}

.nav-title {
  font-family: 'Roboto', sans-serif;
  line-height: calc(20/18);
  text-transform: uppercase;
  letter-spacing: 0.7px;
  font-size: 18px;
  font-size: calc(10px + (18 - 10) * ((100vw - 320px) / (960 - 320)));
}

@media (max-width: 320px) {
  .nav-title {
    font-size: 10px;
  }
}

@media (min-width: 960px) {
  .nav-title {
    font-size: 18px;
  }
}

.nav-title_tab {
  font-size: 17px;
  font-size: 17px;
  font-size: calc(10px + (17 - 10) * ((100vw - 320px) / (960 - 320)));
}

@media (max-width: 320px) {
  .nav-title_tab {
    font-size: 10px;
  }
}

@media (min-width: 960px) {
  .nav-title_tab {
    font-size: 17px;
  }
}

.menu-title {
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  line-height: calc(30/16);
  text-transform: uppercase;
  letter-spacing: 0.7px;
}

.search {
  position: relative;
  display: inline-block;
}

.search__input {
  height: 43px;
  width: 500px;
  width: 500px;
  width: calc(240px + (500 - 240) * ((100vw - 320px) / (960 - 320)));
  padding-left: 50px;
  background: url(../img/search.png) no-repeat;
  background-position: center left 5px;
  border: 1px solid #999999;
}

@media (max-width: 320px) {
  .search__input {
    width: 240px;
  }
}

@media (min-width: 960px) {
  .search__input {
    width: 500px;
  }
}

.search__input[placeholder] {
  color: #333333;
  font-family: Roboto;
  font-size: 22px;
  font-weight: 400;
  line-height: 20px;
}

.search__btn {
  position: absolute;
  right: 0;
  top: 0;
  width: 43px;
  height: 42px;
  background-color: #45B9F4;
  border-bottom: 1px solid #999999;
  color: #fff;
}

.articles {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px 9px;
}

@media only screen and (max-width: 1270px) {
  .articles {
    grid-template-columns: repeat(3, 275px);
    justify-content: center;
  }
}

@media only screen and (max-width: 930px) {
  .articles {
    grid-template-columns: repeat(2, 275px);
  }
}

@media only screen and (max-width: 650px) {
  .articles {
    grid-template-columns: repeat(1, 275px);
  }
}

.article {
  padding: 10px 15px 13px;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.28);
}

.article__img {
  width: 100%;
  height: 212px;
  margin-bottom: 23px;
  background-size: 100% 100%;
}

.article__title {
  margin-bottom: 28px;
  max-height: 20px;
}

.article__text {
  border-top: 1px solid #000;
  border-bottom: 1px solid #000;
  padding-top: 13px;
  height: 105px;
  margin-bottom: 12px;
}

.article__text p {
  margin-top: 9px;
}

.tabulation {
  display: flex;
}

.tabulation__btn {
  display: block;
  padding: 16px 50px 12px 69px;
  padding-left: 42px;
  padding-left: calc(10px + (42 - 10) * ((100vw - 650px) / (960 - 650)));
  padding-right: 42px;
  padding-right: calc(10px + (42 - 10) * ((100vw - 650px) / (960 - 650)));
  background-color: transparent;
}

@media (max-width: 650px) {
  .tabulation__btn {
    padding-left: 10px;
  }
}

@media (min-width: 960px) {
  .tabulation__btn {
    padding-left: 42px;
  }
}

@media (max-width: 650px) {
  .tabulation__btn {
    padding-right: 10px;
  }
}

@media (min-width: 960px) {
  .tabulation__btn {
    padding-right: 42px;
  }
}

.tabulation__btn.is-active {
  background-color: #e2e2e3;
}

.tabulation__btn:hover {
  background-color: #888888;
  color: #fff;
  transition: background-color 1s, color 1s;
}

.tab {
  position: absolute;
  display: none;
  width: 100%;
}

.tab.is-active {
  display: block;
}

.tab__wrapper {
  padding: 34px 25px 22px;
  background-color: #e2e2e3;
  margin-bottom: 10px;
}

@media only screen and (max-width: 400px) {
  .tab__wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }
}

.tab__nav {
  margin-bottom: 27px;
}

.tab__block {
  background-color: #fff;
  padding: 0 23px;
}

@media only screen and (max-width: 400px) {
  .tab__block {
    margin: 0 auto;
    padding: 0 15px;
  }
}

.tab__search {
  padding-top: 34px;
  padding-left: 55px;
  margin-bottom: 100px;
}

@media only screen and (max-width: 660px) {
  .tab__search {
    margin-bottom: 50px;
  }
}

@media only screen and (max-width: 600px) {
  .tab__search {
    padding-left: 0;
  }
}

.tab__content {
  padding-bottom: 92px;
}

@media only screen and (max-width: 450px) {
  .tab__content {
    padding-bottom: 50px;
  }
}

.tab-nav {
  display: flex;
  flex-wrap: wrap;
}

.tab-nav__item {
  border-left: 1px solid #000;
}

.tab-nav__item:first-child {
  border-left-color: transparent;
}

@media only screen and (max-width: 380px) {
  .tab-nav__item {
    width: 50%;
    border-left-color: transparent;
  }
}

.tab-nav__btn {
  display: block;
  background-color: #e2e2e3;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  line-height: calc(20/16);
  letter-spacing: 0.4px;
  padding: 10px 49px 0 44px;
  padding-left: 42px;
  padding-left: calc(10px + (42 - 10) * ((100vw - 650px) / (960 - 650)));
  padding-right: 42px;
  padding-right: calc(10px + (42 - 10) * ((100vw - 650px) / (960 - 650)));
}

@media (max-width: 650px) {
  .tab-nav__btn {
    padding-left: 10px;
  }
}

@media (min-width: 960px) {
  .tab-nav__btn {
    padding-left: 42px;
  }
}

@media (max-width: 650px) {
  .tab-nav__btn {
    padding-right: 10px;
  }
}

@media (min-width: 960px) {
  .tab-nav__btn {
    padding-right: 42px;
  }
}

.tab-nav__btn:hover {
  font-weight: 700;
}

.navigation {
  display: flex;
  justify-content: space-between;
  margin-right: 13px;
}

@media only screen and (max-width: 1070px) {
  .navigation {
    flex-wrap: wrap;
    justify-content: center;
  }
}

@media only screen and (max-width: 570px) {
  .navigation {
    display: none;
  }
}

.navigation__side {
  margin-left: 24px;
  display: flex;
  flex-wrap: wrap;
}

@media only screen and (max-width: 1070px) {
  .navigation__side {
    margin-bottom: 15px;
  }
}

.navigation__item {
  padding-left: 42px;
}

.navigation__item:first-child {
  padding-left: 0;
}

.navigation__item a:after {
  background-color: #45B9F4;
  display: block;
  content: "";
  height: 2px;
  transform: scaleX(0);
  transition: transform .5s;
}

.navigation__item a:hover:after {
  transform: scaleX(1);
  transform-origin: center;
}

.navigation__btn {
  background-color: transparent;
  position: absolute;
  right: 15px;
  top: 5px;
  font-size: 35px;
  display: none;
}

@media only screen and (max-width: 570px) {
  .navigation__btn {
    display: block;
  }
}

.navigation__btn.is-active {
  display: block;
}

.navigation__close {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 20px;
}

.navigation__small {
  position: absolute;
  top: 0;
  background-color: #e2e2e3;
  margin-top: -20px;
  padding: 15px;
  z-index: 100;
  width: 100%;
  display: none;
  border-bottom: 1px solid #fff;
}

.navigation__small.is-active {
  display: block;
}

.icon_nav {
  font-size: 12px;
}

.user {
  position: absolute;
  right: 3px;
  display: flex;
  align-items: flex-end;
}

.user__photo {
  width: 80px;
  height: 79px;
}

.user__img {
  width: 100%;
  height: 100%;
}

.user__greeting {
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-style: italic;
  font-size: 18px;
  line-height: calc(20/18);
  margin-right: 13px;
  margin-bottom: 2px;
}

.menu {
  margin-top: 40px;
  margin-bottom: 20px;
  text-align: center;
}

.menu__item:hover {
  background-color: #888888;
  color: #fff;
  transition: background-color 1s;
}

.menu__item:hover a {
  color: #fff;
  transition: color 1s;
}
